import React from 'react'
import './form.css'
import bookWithMeMandala from '../../assets/bookWithMeMandala.svg'

import { useState } from 'react'
const Form = () => {
 
  const INITIAL_DATA = {
    name: "",
    number: "",
    email: "",
    message: "",
  }
  const [data, setData] = useState(INITIAL_DATA)
  function updateFields(fields) {
    setData(prev => {
      return { ...prev, ...fields }
    })
  }

  const handleSubmit = (e) => {
    e.preventDefault()
    const config = {
      Username: "ommanimeshmishra2@gmail.com",
      Password: "6A6A7D04C549A9E3F5DEA3DDCCCC033C8F9E",
      Host: "smtp.elasticemail.com",
      Port: "2525",
      To: 'ommanimeshmishra2@gmail.com',
      From: 'ommanimeshmishra@gmail.com',
      Subject: "This is the subject",
      Body: data.message
    }
  
    if(window.Email){
      window.Email.send(config).then(()=>alert("email sent"))
    }

  }
  return (
    <div className="form-wrapper">

      <form action="" className='form' onSubmit={handleSubmit}>
        <h1>Get in touch</h1>
        <input
          autoFocus
          required
          type="text"
          value={data.name}
          placeholder="Name"
          onChange={e => updateFields({ name: e.target.value })}
        />

        <input
          required
          type="text"
          value={data.number}
          placeholder="Number"

          onChange={e => updateFields({ number: e.target.value })}
        />

        <input
          required
          type="email"
          value={data.email}
          placeholder="Email"

          onChange={e => updateFields({ email: e.target.value })}
        />

        <input
          required
          type="text"
          value={data.message}
          placeholder="Message"

          onChange={e => updateFields({ message: e.target.value })}
        />
        <button type='submit' onClick={handleSubmit}>Submit</button>
      </form>
    </div>
  )
}

export default Form
