import React from 'react'
import './testimonials.css'
import TestimonialCard from '../TestimonialCard/TestimonialCard'
import ldq from '../../assets/leftDoubleQuotes.svg'
import rdq from '../../assets/rightDoubleQuotes.svg'
import lydq from '../../assets/lydq.png'
import rydq from '../../assets/rydq.png'
const Testimonials = () => {
  return (
    <>
    <div className="testimonials">
        Testimonials
    </div>
      <div className="testimonial-wrapper">
        <div className="testimonial-cards">
           <TestimonialCard backgroundColor="#E6CBAE" color="#917196" rightQ={rdq} leftQ={ldq}/>
           <TestimonialCard backgroundColor="#D6809B" color="#E6CBAE" rightQ={rydq} leftQ={lydq}/>
           <TestimonialCard backgroundColor="#E6CBAE" color="#917196" rightQ={rdq} leftQ={ldq}/>
           <TestimonialCard backgroundColor="#E6CBAE" color="#917196" rightQ={rdq} leftQ={ldq}/>
           <TestimonialCard backgroundColor="#D6809B" color="#E6CBAE" rightQ={rydq} leftQ={lydq}/>
           <TestimonialCard backgroundColor="#E6CBAE" color="#917196" rightQ={rdq} leftQ={ldq}/>
        </div>
      </div>
    </>
  )
}

export default Testimonials
