import React from 'react'
import logo from '../../assets/logo.svg'
import {Link} from 'react-router-dom'
import './navbar.css'
const Navbar = () => {
  return (
    <div className='navbar'>
      <div className="logo">
        <img src={logo} alt="" srcset="" />
        <div className="title">Yogaflow</div>
      </div>
      <div className="navItems">
        <Link style={{textDecoration: 'none'}}  to="Project-Portfolio/"><div className='navlinks'>Home</div></Link>
        <Link style={{textDecoration: 'none'}}  to="Project-Portfolio/about"><div className='navlinks'>About</div></Link>
      {/* <Link style={{textDecoration: 'none'}}  to="/resources"><div className='navlinks'>Resources</div></Link> */}
        <Link style={{textDecoration: 'none'}}  to="Project-Portfolio/services"><div className='navlinks'>Services</div></Link>
        <Link style={{textDecoration: 'none'}}  to="Project-Portfolio/faqs"><div className='navlinks'>FAQ</div></Link>
        <Link style={{textDecoration: 'none'}}  to="Project-Portfolio/bookwithme"><div className='navlinks'>Contact Me</div></Link>
      </div>
    </div>
  )
}

export default Navbar
