import { useState } from "react";
import dropDownUp from '../../assets/dropDownUp.png'
import dropDownDown from '../../assets/dropDownDown.png'
import './faq-boxes.css'
const FAQBoxes = ({ question, answer }) => {
  const [toggle, setToggle] = useState(false);
  console.log(toggle)
  return (
    <div className="faq-box-wrapper">
    <div className="faqs-section-items">
      <div className="question">
        Q. {question}
      </div>
        <div className="dropdown-toggle" onClick={() => setToggle(!toggle)}>
          <img src={toggle ? dropDownUp : dropDownDown} alt="" srcset="" />
        </div>
    </div>
      {toggle ? (
        <div className="answer">
          A. {answer}
        </div>
      ) : null}
    </div>
  );
};

export default FAQBoxes