import React from 'react'
import { Routes, Route } from "react-router-dom";
import Homepage from '../pages/Homepage/Homepage';
import About from '../pages/About/About';
import FAQs from '../pages/FAQs/FAQs';
import BookWithMe from '../pages/BookWithMe/BookWithMe';
const BasicRoutes = () => {
  return (
    <Routes>
      <Route exact path={"Project-Portfolio/"} element={<Homepage />} />
      <Route exact path={"Project-Portfolio/about"} element={<About />} />
      <Route exact path={"Project-Portfolio/faqs"} element={<FAQs />} />
      <Route exact path={"Project-Portfolio/bookwithme"} element={<BookWithMe />} />
      <Route path={"*"} element={<>Well we got this</>} />
    </Routes>

  )
}

export default BasicRoutes
