import React from 'react'
import { Link } from 'react-router-dom'
import './footer.css'
import foot from '../../assets/foot.svg'
import twitter from '../../assets/twitter.png'
import insta from '../../assets/insta.png'
import linkedin from '../../assets/linkedin.png'
const Footer = () => {
  return (
    <div className='footer'>
      <div className="socials">
        <div className="follow-text">
          Follow us at
        </div>
        <div className="follow-icons">
          <div className="ins"><img src={insta} alt="" srcset="" /></div>
          <div className="twit"><img src={twitter} alt="" srcset="" /></div>
          <div className="link"><img src={linkedin} alt="" srcset="" /></div>
        </div>
      </div>
        <div className="footer-links">
          <div className="links-container">

          <Link style={{ textDecoration: 'none' }} to="Project-Portfolio/"><div className='foot-nav-links'>Home</div></Link>
          <Link style={{ textDecoration: 'none' }} to="Project-Portfolio/about"><div className='foot-nav-links'>About</div></Link>
          {/* <Link style={{textDecoration: 'none'}}  to="/resources"><div className='foot-nav-links'>Resources</div></Link> */}
          <Link style={{ textDecoration: 'none' }} to="Project-Portfolio/services"><div className='foot-nav-links'>Services</div></Link>
          <Link style={{ textDecoration: 'none' }} to="Project-Portfolio/faqs"><div className='foot-nav-links'>FAQ</div></Link>
          <Link style={{ textDecoration: 'none' }} to="Project-Portfolio/bookwithme"><div className='foot-nav-links'>Contact Me</div></Link>
          </div>
          <div className="further-contact">
            <h1>Further Information</h1>
            <button className='footer-btn'>Get in touch</button>

          </div>
        </div>
    </div>
  )
}

export default Footer
