import React from 'react'
import './faq.css'
import FAQsBackground from '../../assets/FAQsBackground.png'
import FAQBoxes from '../../components/FAQBoxes/FAQBoxes'
import leftDecor from '../../assets/leftDecor.svg'
import rightDecor from '../../assets/rightDecor.svg'
import mandalaDecor from '../../assets/mandalaDecor.svg'
import leaf from '../../assets/leaf.svg'
import {motion as m} from 'framer-motion'

const FAQs = () => {
  return (
    <>
       <m.main  
  initial={{opacity:0}}
  animate={{opacity:1}}
  transition={{duration:0.75,ease:"easeOut"}}>

      <div className='elongated-page'>
    
        <div className="faqs-container">

          <p className='faq-header'>Frequently Asked Questions</p>
          <p className='faq-about'>
            Here are some of our FAQs. If you have any other questions you'd like
            answered please feel free to email us.
          </p>

          <div className="">
            <FAQBoxes
              question="What is a bookmark?"
              answer="Lorem ipsum dolor sit amet consectetur adipisicing elit. Ratione repudiandae provident repellendus quasi. Hic."
            />
            <FAQBoxes
              question="How can I request a new browser?"
              answer="Lorem ipsum dolor sit amet consectetur adipisicing elit. Ratione repudiandae provident repellendus quasi. Hic."
            />
            <FAQBoxes
              question="Is there a mobile app?"
              answer="Lorem ipsum dolor sit amet consectetur adipisicing elit. Ratione repudiandae provident repellendus quasi. Hic."
            />
            <FAQBoxes
              question="What about other Chromium browsers?"
              answer="Lorem ipsum dolor sit amet consectetur adipisicing elit. Ratione repudiandae provident repellendus quasi. Hic."
            />
            <div className="faq-know-more">
              <section>
              <div className="know-more-img"><img src={mandalaDecor} alt="" srcset="" /></div>
              <div className="know-more-text">Still can't find answers, Contact Us!</div>
                
              </section>
              <div className="know-more-btn">Get in touch</div>
            </div>
          </div>
        </div>
      </div>
      </m.main>
    </>
  )
}

export default FAQs
