import React from 'react'
import './testimonial-card.css'
import pic from '../../assets/picture.jpg'

const TestimonialCard = (props) => {
 const {backgroundColor,color,leftQ,rightQ}=props
  return (
    <>
      <div className="testimonial-card-wrapper">
        <div className="circular-img"><img src={pic} alt="" /></div>
        <div className="testimonial-text" style={{backgroundColor:backgroundColor, color:color}}>
          <img src={leftQ} alt="" />
          <p>Lorem ipsum dolor sit amet consectetur adipisicing elit. Atque nihil nulla dolorum ullam ut sint!</p>
          <img src={rightQ} alt="" />
        </div>
      </div>
    </>
  )
}

export default TestimonialCard
