import React from 'react'
import './bookwithme.css'
import leftDecor from '../../assets/leftDecor.svg'
import rightDecor from '../../assets/rightDecor.svg'
import mandalaDecor from '../../assets/mandalaDecor.svg'
import leaf from '../../assets/leaf.svg'
import leftFormDecor from '../../assets/leftFormDecor.png'
import rightFormDecor from '../../assets/rightFormDecor.png'
import Form from '../../components/Form/Form'
const BookWithMe = () => {
  return (
    <>
    <div className='form-height'>
  <div className="form-container">
    <Form/>
  </div>
  <div className="form-decors">
    <div className="right"><img src={rightFormDecor} alt="" /></div>
    <div className="left"><img src={leftFormDecor} alt="" /></div>
  </div>
  </div>
    </>
  )
}

export default BookWithMe
